body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home-link {
  transition: all 0.2s ease-in-out;
}
.home-link:hover {
  transform: scale(1.1);
}

.main-content {
  margin-top: 50px;
}
.page-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.page-links a {
  text-decoration: none;
  width: 200px;
  padding: 10px;
  margin: 10px;
  border-radius: 4px;
  background: rgba(56, 77, 139);
  opacity: 0.95;
  /* background: rgb(13, 138, 240); */
  color: white;
  box-shadow: 1px 3px 6px -1px rgba(27, 40, 78, 0.9);
}
.page-links a:hover {
  opacity: 1;
}
.animation-divs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.controls {
  display: flex;
  justify-content: space-between;
  padding: 5px;
}
.controls * {
  margin: 5px;
}
.page1-heading {
  text-align: center;
  text-decoration: underline;
}
li {
  margin: 15px;
}
.sub-title-name {
  color: red;
  font-style: italic;
}

.sub-title-code {
  color: green;
}

.sub-title-file {
  color: blue;
}
